<template>
    <div class="form-section">
        <div v-bind:class="isDisabled">
            <message-input
                v-bind:onSubmit="onSubmit"
                v-bind:users="users"
                v-bind:placeholderText="placeholder">
            </message-input>
            <status-text v-bind:status="status"></status-text>
        </div>
    </div>
</template>

<script>
import Vue from 'vue';
import StatusText from './StatusText.vue'
import MessageInput from './MessageInput.vue'
import { sendMessage } from '../utils';

export default {
    name: 'form-section',
    props: ['window', 'users', 'status', 'atMentions'],
    components: {
        StatusText, MessageInput
    },
    computed: {
        placeholder: function() {
            return `Message ${this.window ? this.window.name : ""}`;
        },
        isDisabled: function() {
            return (this.window && this.window.isDisabled) ? "disabled" : "";
        }
    },
    methods: {
        onSubmit: function(text) {
            const type = text.startsWith("/") ? "command" : "text";
            sendMessage(type, text);
        }
    },
    mounted() {
        return sendMessage("is_ready");
    }
}
</script>

<style>
.disabled {
    display: none;
}
</style>
